.contact {
    display: grid;
    justify-items: center;
    align-items: center;
    background: url('../../../components/assets/img/Fondo_Kapta/Fondo06.png') no-repeat;
    background-size: cover;
    height: 100vh;
    width: 100vw;
    padding: 20%;
}

.contact img {
    width: 50%;
}

.contact p {
    font-size: 1.1em;
    color: black;
}


@media (min-width: 1024px) {
    .contact {
        background: url('../../../components/assets/img/Fondo_Kapta/Fondo06.png') no-repeat center;
        background-size: cover;
        height: 100%;
    }
}


@media (min-width: 1900px) {
    .contact {
        padding: 30%;
    }
    .contact p {
        font-size: 1.5em;   
    }
}
@media (max-width: 765px) {
    .contact {
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        background: url('../../../components/assets/img/Fondo_Kapta/Fondo06.png') no-repeat center center;
        background-size: cover;
        height: 100%;
        padding: 5em 10px;
    }

    .contact img {
        width: 60%;
    }
    
    .contact p {
        font-size: 1.1em;
        color: black;
    }
}
