.mantenimiento {
    display: grid;
    justify-items: center;
    align-items: center;
    grid-template-rows: 1fr 1fr;
    background: url('../assets/img/carrusel/fondo_transparencia.png') no-repeat center center;
    background-size: cover;
    height: 100vh;
    width: 100vw;
}

.mantenimiento .logo {
    display: flex;
    justify-items: center;
}

.mantenimiento .logo img {
    width: 50%;
}